import { Component, inject, output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { SessionGroupsModel, SessionPhotoModel, SessionRecordingModel } from 'src/app/api/model/models';

import { SessionConstants } from 'src/app/constants/session-constants';
import { ConfirmationModalComponent } from 'src/app/modules/shared/components/confirmation-modal/confirmation-modal.component';

import { SessionService } from '../../session.service';
import { DetailsService } from '../details.service';
import { EditService } from '../tabs/edit/edit.service';

import { MediaState } from 'src/app/modules/shared/state/media/media.service';

interface SelectedFileData {
  session: SessionGroupsModel;
  file: SessionRecordingModel | SessionPhotoModel;
  type: string;
}

@Component({
  selector: 'session-list',
  standalone: false,
  templateUrl: './session-list.component.html',
  styleUrl: './session-list.component.scss'
})
export class SessionListComponent {
  private readonly dialogRef = inject(MatDialog);
  private readonly mediaState = inject(MediaState);

  private readonly sessionService = inject(SessionService);
  private readonly detailsService = inject(DetailsService);
  private readonly editService = inject(EditService);

  // * Details service signals
  $currentSelection = this.detailsService.currentSelection;
  $componentHeights = this.detailsService.componentHeights;

  // * Media state signals
  $fileSource = this.mediaState.file_source;

  $logsEvent = output<boolean>({ alias: 'logsEvent' });

  sessionExpandedList: boolean[];

  readonly sessionTypes = SessionConstants.sessionTypes;
  readonly fileLocationNames = SessionConstants.fileLocationNames;
  readonly fileLocations = SessionConstants.fileLocations;

  constructor() {
    this.sessionExpandedList = Array(this.sessionsList.length).fill(false);

    const session = this.detailsService.session_object;
    this.sessionExpandedList[this.sessionsList.indexOf(session)] = true;
  }

  get sessionsList(): SessionGroupsModel[] {
    return this.sessionService.sessionsList;
  }

  get listComponentHeight(): number {
    const ch = this.$componentHeights();

    // * player-height + metaComponent-height + tabsComponent-height
    const calculatedHeight = 460 + ch.metadataComponent + ch.tabComponent;
    return calculatedHeight;
  }

  onDropdownToggle(state: boolean, index: number) {
    this.sessionExpandedList[index] = state;
  }

  openDialog() {
    return this.dialogRef.open(ConfirmationModalComponent, {
      data: {
        title: `You have unsaved changes in this file. Are you sure to discard these changes?`
      }
    });
  }

  updateSelectedFile({ session, file, type }: SelectedFileData) {
    this.editService.isAudioGenerated = false;

    this.detailsService.updateCurrentSelection({
      session_id: session._session_id,
      file_id: file.uniqueFileName,
      file_type: type
    });

    const rec = this.detailsService.session_rec_object;
    const photo = this.detailsService.session_photo_object;

    this.detailsService.updateErrorState({
      _canShowFile: type.includes('photo')
        ? photo.fileLocation === this.fileLocations.dock || photo.fileLocation === this.fileLocations.central
        : rec.fileLocation === this.fileLocations.dock || rec.fileLocation === this.fileLocations.central
    });

    // - Resetting player data
    this.mediaState.resetState(['player', 'tracks']);

    this.mediaState.updateFileSource({
      videoSource: rec?.['<ResourceURL>k__BackingField'] ?? '',
      videoThumbnail: '',
      audioSource: rec?.['<ResourceURL>k__BackingField'] ?? '',
      imageSource: photo?.['<ResourceURL>k__BackingField'] ?? ''
    });

    // * Only when photo file is selected, update the loader of edit UI here..
    if (type.includes('photo')) {
      this.editService.editObservable = 'loaded';
      this.editService.editObservable = '';
    }

    // * Sending boolean value to trigger "onSelectFile" in media player
    // * Send "true" only for recordings and not for photo
    // * That's why the condition below
    this.$logsEvent.emit(!type.includes('photo'));
  }

  onSelectFile({ session, file, type }: SelectedFileData) {
    if (this.editService.edited) {
      this.openDialog()
        .afterClosed()
        .subscribe((result) => {
          if (result) {
            // * Updating this to trigger reset in Edit UI
            this.editService.editObservable = 'on_file_select';
            this.editService.editObservable = 'loading';
            this.updateSelectedFile({ session, file, type });
          }
        });
    } else {
      this.editService.editObservable = 'loading';
      this.updateSelectedFile({ session, file, type });
    }
  }

  onSessionSelect(event: HTMLInputElement, id: string) {
    this.detailsService.updateSelectedSessions(id, event.checked ? 'add' : 'remove');
  }

  ngOnInit() {}
}
