import { Component, input, output } from '@angular/core';

import { SessionConstants } from 'src/app/constants/session-constants';
import { SessionGroupsModel, SessionPhotoModel, SessionRecordingModel } from 'src/app/api/model/models';

interface SelectFileEvent {
  session: SessionGroupsModel;
  file: SessionRecordingModel | SessionPhotoModel;
  type: string;
}

@Component({
  selector: 'card',
  standalone: false,
  templateUrl: './card.component.html',
  styleUrl: './card.component.scss'
})
export class CardComponent {
  readonly sessionTypes = SessionConstants.sessionTypes;

  $session = input.required<SessionGroupsModel>({ alias: 'session' });
  $isDropdownExpanded = input.required<boolean>({ alias: 'isDropdownExpanded' });

  $toggleEvent = output<boolean>({ alias: 'toggleEvent' });
  $selectFileEvent = output<SelectFileEvent>({ alias: 'selectFileEvent' });

  get username(): string {
    return this.$session()['<CreatorUser>k__BackingField']._username;
  }

  get sessionType(): number {
    return this.$session()['<SessionType>k__BackingField'];
  }

  onDropdownToggle() {
    this.$toggleEvent.emit(!this.$isDropdownExpanded());
  }

  onSelectFile(data: SelectFileEvent) {
    this.$selectFileEvent.emit(data);
  }
}
