import { Component, inject, output } from '@angular/core';
import { Location } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';

import { DetailsService } from '../details.service';
import { AddCaseComponent } from '../../components/add-case/add-case.component';

@Component({
  selector: 'details-header',
  standalone: false,
  templateUrl: './details-header.component.html',
  styleUrl: './details-header.component.scss'
})
export class DetailsHeaderComponent {
  private readonly dialog = inject(MatDialog);
  private readonly location = inject(Location);
  private readonly detailsService = inject(DetailsService);

  $loadingEvent = output<boolean>({ alias: 'loadingEvent' });
  $logsEvent = output<boolean>({ alias: 'logsEvent' });

  isLoading = false;

  constructor() {}

  get session_info() {
    return {
      id: this.detailsService.session_object._vod_id ?? '...',
      name: this.detailsService.session_object['<CreatorUser>k__BackingField']._username ?? '...'
    };
  }

  get canAddCase(): boolean {
    return this.detailsService.selectedSessions().length !== 0;
  }

  goBack() {
    this.location.back();
  }

  toggleAddToCase() {
    if (!this.canAddCase) return;

    const dialog = this.dialog.open(AddCaseComponent, {
      disableClose: false
    });

    dialog.afterClosed().subscribe((result) => {
      if (result) console.log('Add case');
    });
  }

  ngOnDestroy() {}
}
