import { Component, inject, input, output } from '@angular/core';

import { FileLocationNamesTypes, SessionConstants } from 'src/app/constants/session-constants';
import { SessionGroupsModel, SessionPhotoModel, SessionRecordingModel } from 'src/app/api/model/models';
import { DetailsService } from '../../details.service';

@Component({
  selector: 'file',
  standalone: false,
  templateUrl: './file.component.html',
  styleUrl: './file.component.scss'
})
export class FileComponent {
  private readonly detailsService = inject(DetailsService);
  $currentSelection = this.detailsService.currentSelection;

  $filetype = input.required<string>({ alias: 'filetype' });
  $session = input.required<SessionGroupsModel>({ alias: 'session' });
  $recFile = input<SessionRecordingModel>(undefined, { alias: 'recFile' });
  $photoFile = input<SessionPhotoModel>(undefined, { alias: 'photoFile' });

  $selectFileEvent = output<void>({ alias: 'selectFileEvent' });

  fileLocationNames = SessionConstants.fileLocationNames;
  fileLocations = SessionConstants.fileLocations;

  constructor() {}

  get selectedFile(): boolean {
    return this.$filetype().includes('photo')
      ? this.$photoFile()?.uniqueFileName === this.$currentSelection().file_id
      : this.$recFile()?.uniqueFileName === this.$currentSelection().file_id;
  }

  get fileNotExists(): boolean {
    let notExists: boolean;

    if (this.$filetype().includes('photo')) {
      notExists =
        this.fileLocations.central !== this.$photoFile()!.fileLocation &&
        this.fileLocations.dock !== this.$photoFile()!.fileLocation;
    } else {
      notExists =
        this.fileLocations.central !== this.$recFile()!.fileLocation &&
        this.fileLocations.dock !== this.$recFile()!.fileLocation;
    }

    return notExists;
  }

  // * Checking if the file is "redaction in progress"
  get redactionInProgress(): boolean {
    let inProgress: boolean;

    if (this.$filetype().includes('photo')) {
      // ! Redaction does not apply for image files
      inProgress = false;
    } else {
      inProgress =
        this.$recFile()!['<RedactedStatus>k__BackingField'] !== -1 &&
        this.$recFile()!['<RedactedStatus>k__BackingField'] !== 11 &&
        this.$recFile()!['<RedactedStatus>k__BackingField'] !== 13;
    }

    return inProgress;
  }

  get cantShowFile(): boolean {
    let cantShow: boolean;

    if (this.$filetype().includes('photo')) {
      cantShow = false;
    } else {
      cantShow = this.$recFile()!['<RedactedStatus>k__BackingField'] === 11;
    }

    return cantShow;
  }

  get fileType(): string {
    let type: string;

    if (this.$filetype().includes('photo')) type = 'Image';
    else {
      // * Multiple checks for
      //   * Redacted, Redaction in progress, Review Redaction
      //   * Beeped, Clipped
      //   * Pre-record
      //   * Normal Audio & Video file

      // TODO: Pre record and clipping conditions to be modified
      const copyOf = this.$recFile()!['<CopyOf>k__BackingField'];
      const isOriginalFile: boolean = copyOf < 0 ? true : false; // ! Original files have "<CopyOf>" parameter in negative values (-214673453)

      let isClipped: boolean = false;

      // * Condition false for original file
      if (!isOriginalFile) {
        const originalFile: SessionRecordingModel = this.$session()._recordings.filter(
          (rec) => rec['<CopyOf>k__BackingField']! < 0
        )[0];

        // * Check if the current file has start and end time as same as of original file
        if (
          originalFile.startiTme !== this.$recFile()!.startiTme &&
          originalFile.finishTime !== this.$recFile()!.finishTime
        )
          isClipped = true;
      }

      if (this.$recFile()!['<RedactedStatus>k__BackingField'] === 13) {
        type = 'Redacted';
      } else if (this.$recFile()!['<RedactedStatus>k__BackingField'] === 4) {
        type = 'Review Redaction';
      } else if (
        this.$recFile()!['<RedactedStatus>k__BackingField'] !== -1 &&
        this.$recFile()!['<RedactedStatus>k__BackingField'] !== 11
      ) {
        type = 'Redaction in progress';
      } else if (this.$recFile()!['<BeepingInterval>k__BackingField'] !== '') {
        type = 'Beeped';
      } else if (isClipped) {
        type = 'Clipped';
      } else if (this.$session()._containsPrerecord) {
        type = 'Pre-record enabled video';
      } else if (this.$filetype().includes('audio')) {
        type = 'Audio';
      } else {
        type = 'Video';
      }
    }

    return type;
  }

  get fileLocation(): string {
    return this.fileLocationNames[
      this.$filetype().includes('photo')
        ? (this.$photoFile()!.fileLocation as FileLocationNamesTypes)
        : (this.$recFile()!.fileLocation as FileLocationNamesTypes)
    ];
  }

  onSelectFile() {
    if (this.redactionInProgress || this.fileNotExists) return;

    this.$selectFileEvent.emit();
  }
}
