import { Component, ElementRef, inject, TemplateRef, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';

import { CanDeactivateType } from './edit.guard';
import { ConfirmationModalComponent } from 'src/app/modules/shared/components/confirmation-modal/confirmation-modal.component';

import { MediaState } from 'src/app/modules/shared/state/media/media.service';
import { SessionConstants, RedactStatusModel, RedactStatusModelTypes } from 'src/app/constants/session-constants';

import { EditService } from './edit.service';
import { DetailsService } from '../../details.service';

import { CalculationHelpers } from 'src/app/modules/shared/helpers/calculations';
import { AlertHelpersService } from 'src/app/modules/shared/helpers/alert-helpers';

import { BeepingApiService, ClippingApiService, RedactApiService } from 'src/app/api';
import {
  ClippingModel,
  BeepingRequestModel,
  BeepingResponseModel,
  ProceedRedactResponse,
  ReviewRedactResponse
} from 'src/app/api/model/models';
import { SessionData } from 'src/app/data/session.data';

interface BeepsCreated {
  id: number;
  startPosition: number;
  endPosition: number;
  width: number;
}

interface BeepTimings {
  startTime: string;
  endTime: string;
}

@Component({
  selector: 'app-edit',
  standalone: false,
  templateUrl: './edit.component.html',
  styleUrl: './edit.component.scss'
})
export class EditComponent {
  private readonly dialogRef = inject(MatDialog);
  private readonly mediaState = inject(MediaState);

  private readonly detailsService = inject(DetailsService);
  private readonly editService = inject(EditService);

  private readonly clippingApiService = inject(ClippingApiService);
  private readonly beepingApiService = inject(BeepingApiService);
  private readonly redactApiService = inject(RedactApiService);

  private readonly calculationHelpers = inject(CalculationHelpers);
  private readonly alertHelpers = inject(AlertHelpersService);

  @ViewChild('trackpadCursorRef') readonly trackpadCursorRef!: ElementRef;
  @ViewChild('waveformCanvas') readonly waveformCanvas!: ElementRef<HTMLCanvasElement>;
  @ViewChild('trackpadFramesContainerRef') readonly trackpadFramesContainerRef!: ElementRef;
  @ViewChild('dialogLoader', { static: true }) readonly dialogLoaderRef!: TemplateRef<any>;

  // * Details service signals
  $currentSelection = this.detailsService.currentSelection;
  $errorState = this.detailsService.errorState;

  // * Media state signals
  $playerState = this.mediaState.player_state;
  $tracksState = this.mediaState.tracks_state;
  $mediaRefs = this.mediaState.media_refs;
  $editState = this.mediaState.edit_state;

  templateDialog?: MatDialogRef<any>;
  isLoading = false;
  saving = false;

  undo = false;
  redo = false;
  undoState = [];
  redoState = [];
  undoCount = 0;
  redoCount = 0;

  isCursorDragging = false;
  isStickDragging = false;
  isLeftStickDragging = false;
  isRightStickDragging = false;

  clippingActivated = false;
  outputTypes: string[] = ['Audio', 'Video', 'Audio & Video'];
  clippingOutputSelected = false;
  selectedOutput: string = '';
  outputType: 'A' | 'V' | 'AV' | '' = '';

  isHandleDragging = false;
  isLeftHandleDragging = false;
  isRightHandleDragging = false;

  id = 0;
  beepingActivated = false;
  beepsCreated: BeepsCreated[] = [];
  beepTimings: BeepTimings[] = [];
  isDrawing = false;

  redactActivated = false;
  redact_status_enum: RedactStatusModel = SessionConstants.redact_status;

  constructor() {}

  canDeactivate(): CanDeactivateType {
    if (!this.edited || this.clippingOutputSelected) {
      return true;
    } else {
      const dialogRef = this.dialogRef.open(ConfirmationModalComponent, {
        panelClass: 'material',
        disableClose: true,
        data: {
          title: 'You have unsaved changes in this file. Are you sure you want to leave these changes?',
          confirmationText: 'Leave',
          cancelText: 'Stay'
        }
      });
      dialogRef.afterClosed().subscribe((result) => {
        if (result) {
          this.resetClipping();
          this.resetBeeping();
        }
      });

      return dialogRef.afterClosed();
    }
  }

  get saveText(): string {
    return `Create ${this.beepingActivated ? 'Beep' : ''}${this.clippingActivated ? 'Clip' : ''} `;
  }

  get canShowEditingTools() {
    return !this.$currentSelection().file_type.includes('photo');
  }

  get edited() {
    return this.editService.edited;
  }

  get isAudioGenerated() {
    return this.editService.isAudioGenerated;
  }

  get redacted_status() {
    const recordings = this.detailsService.session_object._recordings;
    const video_id = this.detailsService.session_rec_object.videoId;

    /*
      * Filtering out the redacted session_rec object
      * Checking the correct file with video id === copyOf
      TODO: In future, also add logic to check - only the user that "Process" the file for redact can actually "Review" it
    */

    const redact = recordings.filter(
      (rec) =>
        rec['<CopyOf>k__BackingField'] === video_id &&
        rec['<RedactedStatus>k__BackingField'] !== -1 &&
        rec['<RedactedStatus>k__BackingField'] !== 11 &&
        rec['<RedactedStatus>k__BackingField'] !== 13
    );

    return this.redact_status_enum[redact[0]['<RedactedStatus>k__BackingField'] as RedactStatusModelTypes];
  }

  get redaction_btn_enabled() {
    if (this.redacted_status.includes('detected')) return true;
    else return false;
  }

  get cant_redact() {
    const rec = this.detailsService.session_rec_object;

    // * Can't redact if it's already redacted
    return rec['<RedactedStatus>k__BackingField'] === 13;
  }

  set edited(state: boolean) {
    this.editService.edited = state;
  }

  getIntervalTime(index: number) {
    let interval_time: string;

    if (this.beepTimings[index]) {
      interval_time = `${this.beepTimings[index].startTime} - ${this.beepTimings[index].endTime}`;
    } else {
      interval_time = `--:--:--.--- - --:--:--.---`;
    }

    return interval_time;
  }

  resetClipping() {
    this.mediaState.updateEditState({
      trimStart: 0,
      trimEnd: this.$playerState().duration
    });

    this.mediaState.updateTracksState({
      trackpad_left_ph_width: 0,
      trackpad_stick_left_position: 0,
      trackpad_right_ph_width: 0,
      trackpad_stick_right_position: 0,
      trackpad_audio_handle_left_position: 0,
      trackpad_audio_handle_right_position: 0,
      trackpad_audio_handle_width: this.$tracksState().fixed_width
    });
    this.clippingActivated = this.edited = this.clippingOutputSelected = false;
  }

  resetBeeping() {
    this.beepTimings = [];
    this.beepsCreated = [];
    this.edited = this.beepingActivated = false;
    this.id = 0;
  }

  activateEditing(edit_type: string) {
    this.redactActivated = false;

    if (edit_type === 'clip') this.activatingClip(edit_type);
    else if (edit_type === 'beep') this.activatingBeep(edit_type);
  }

  openDialog(edit_type: string, alt?: boolean) {
    const dialog = this.dialogRef.open(ConfirmationModalComponent, {
      data: {
        title: `You have unsaved changes in this file. Are you sure to discard these changes?`
      }
    });

    dialog.afterClosed().subscribe((result) => {
      if (result) {
        if (edit_type === 'clip') {
          if (alt) {
            this.resetBeeping();
            this.clippingActivated = true;
          } else {
            this.resetClipping();
          }
        } else if (edit_type === 'beep') {
          if (alt) {
            this.resetClipping();
            this.beepingActivated = true;
          } else {
            this.resetBeeping();
          }
        }
      }
    });
  }

  openTemplateDialog(templateRef: TemplateRef<any>) {
    this.templateDialog = this.dialogRef.open(templateRef, {
      disableClose: true
    });
  }

  activatingClip(edit_type: string) {
    if (this.beepingActivated) {
      if (this.edited) this.openDialog(edit_type, true);
      else {
        this.beepingActivated = false;
        this.clippingActivated = true;
      }
    } else if (this.clippingActivated) {
      if (this.edited || this.clippingOutputSelected) this.openDialog(edit_type);
      else this.clippingActivated = false;
    } else if (!this.clippingActivated) {
      this.clippingActivated = true;
    }

    this.selectedOutput = this.$currentSelection().file_type.includes('audio') ? 'Audio' : 'Audio & Video';
    this.outputType = this.$currentSelection().file_type.includes('audio') ? 'A' : 'AV';
  }

  activatingBeep(edit_type: string) {
    if (this.clippingActivated) {
      if (this.edited || this.clippingOutputSelected) this.openDialog(edit_type, true);
      else {
        this.clippingActivated = false;
        this.beepingActivated = true;
      }
    } else if (this.beepingActivated) {
      if (this.edited) this.openDialog(edit_type);
      else this.beepingActivated = false;
    } else if (!this.beepingActivated) {
      this.beepingActivated = true;
    }
  }

  // * Clipping controls

  // - Video clipping
  onCursorGrab() {
    this.isCursorDragging = true;
    this.trackpadCursorRef.nativeElement.classList.add('cursor-grabbing');

    // document.body.classList.add('cursor-grabbing');
    this.trackpadCursorRef.nativeElement.classList.add('cursor-grabbing');
    document.addEventListener('mousemove', this.trackCursorPosition.bind(this));
    document.addEventListener('mouseup', this.onCursorRelease.bind(this));
  }

  onCursorRelease() {
    if (this.isCursorDragging) {
      this.isCursorDragging = false;
      this.trackpadCursorRef.nativeElement.classList.remove('cursor-grabbing');

      // document.body.classList.remove('cursor-grabbing');
      this.trackpadCursorRef.nativeElement.classList.remove('cursor-grabbing');
      document.removeEventListener('mousemove', this.trackCursorPosition.bind(this));
      document.removeEventListener('mouseup', this.onCursorRelease.bind(this));
    }
  }

  onEditTrackStickGrab(direction: string) {
    this.isCursorDragging = true;
    this.isStickDragging = true;

    if (direction === 'left') this.isLeftStickDragging = true;
    else this.isRightStickDragging = true;

    this.trackpadCursorRef.nativeElement.classList.add('cursor-resize');
    document.addEventListener('mousemove', this.trackCursorPosition.bind(this));
    document.addEventListener('mouseup', this.onStickRelease.bind(this));
  }

  onStickRelease() {
    if (this.isStickDragging) {
      this.isStickDragging = false;
      this.isCursorDragging = false;
      this.isLeftStickDragging = false;
      this.isRightStickDragging = false;

      this.trackpadCursorRef.nativeElement.classList.remove('cursor-resize');
      document.removeEventListener('mousemove', this.trackCursorPosition.bind(this));
      document.removeEventListener('mouseup', this.onStickRelease.bind(this));
    }
  }

  trackCursorPosition(event: any) {
    if (this.isCursorDragging) {
      this.editService.calculateProgressTiming(
        event,
        this.isStickDragging,
        this.isLeftStickDragging ? 'left' : 'right'
      );

      if (
        this.$editState().trimStart !== 0 ||
        (this.$editState().trimEnd !== 0 && this.$editState().trimEnd !== this.$playerState().duration)
      ) {
        this.edited = true;
      } else {
        if (!this.clippingOutputSelected) this.edited = false;
      }
    }
  }

  onOutputSelect(type: string) {
    if (type.split('&').length > 1) this.outputType = 'AV';
    else this.outputType = type[0] as 'A' | 'V' | 'AV';

    this.edited = true;
    this.clippingOutputSelected = true;
  }

  // - Audio clipping
  onAudioHandleGrab(event: MouseEvent, direction: string) {
    this.isHandleDragging = true;

    if (direction === 'left') this.isLeftHandleDragging = true;
    else this.isRightHandleDragging = true;

    document.addEventListener('mousemove', this.trackHandlePosition.bind(this));
    document.addEventListener('mouseup', this.onAudioHandleRelease.bind(this));
  }

  onAudioHandleRelease() {
    if (this.isHandleDragging) {
      this.isHandleDragging = false;
      this.isLeftHandleDragging = false;
      this.isRightHandleDragging = false;

      document.removeEventListener('mousemove', this.trackHandlePosition.bind(this));
      document.removeEventListener('mouseup', this.onAudioHandleRelease.bind(this));
    }
  }

  trackHandlePosition(event: MouseEvent) {
    if (this.isHandleDragging) {
      this.editService.calculateAudioClipping(event, this.isLeftHandleDragging ? 'left' : 'right');
    }

    if (this.$editState().trimStart !== 0 || this.$editState().trimEnd !== this.$playerState().duration) {
      this.edited = true;
    } else {
      this.edited = false;
    }
  }

  // * Beeping Controls
  createBeep(event: MouseEvent) {
    const rect = this.trackpadFramesContainerRef!.nativeElement.getBoundingClientRect();
    const clientPosition = event.clientX - rect.left;

    this.beepsCreated.push({
      id: this.id + 1,
      startPosition: clientPosition,
      endPosition: 0,
      width: 0
    });
  }

  onDrawingInitiate(event: MouseEvent) {
    if (this.beepingActivated) {
      if (event.button === 2) return;

      this.isDrawing = true;
      this.createBeep(event);

      document.addEventListener('mouseup', this.onDrawingDone.bind(this));
    }
  }

  onDraw(event: MouseEvent) {
    if (this.isDrawing) {
      this.calculateBeeping(event);
    }
  }

  onDrawingDone() {
    if (this.isDrawing) {
      this.isDrawing = false;
      document.removeEventListener('mousemove', this.onDrawingDone.bind(this));

      if (this.beepsCreated[this.id].endPosition === 0) {
        this.beepsCreated.pop();
        this.beepTimings.pop();
      } else {
        this.id++;
        this.edited = true;
      }
    }
  }

  calculateBeeping(event: MouseEvent) {
    const rect = this.trackpadFramesContainerRef!.nativeElement.getBoundingClientRect();
    let clientPosition = event.clientX - rect.left;

    const start_position = this.beepsCreated[this.id].startPosition;

    if (clientPosition > this.$tracksState().trackpad_content_width - 20)
      clientPosition = this.$tracksState().trackpad_content_width - 20;

    if (clientPosition > start_position) {
      this.beepsCreated[this.id].width = clientPosition - start_position;
      this.beepsCreated[this.id].endPosition = clientPosition;

      const start_percentage = (start_position / this.$tracksState().trackpad_content_width) * 100;
      const end_percentage = (clientPosition / this.$tracksState().trackpad_content_width) * 100;

      const start_time = (start_percentage / 100) * this.$playerState().duration;
      const end_time = (end_percentage / 100) * this.$playerState().duration;

      this.beepTimings[this.id] = {
        startTime: this.calculationHelpers.secondsToTime(start_time, 'hrs_ms'),
        endTime: this.calculationHelpers.secondsToTime(end_time, 'hrs_ms')
      };
    }
  }

  onBeepIntervalClick(event: MouseEvent) {
    event.stopPropagation();
    if (event.button === 2) return;
  }

  deleteBeepInterval(id: number) {
    this.beepsCreated.splice(id, 1);
    this.beepTimings.splice(id, 1);
    this.id--;

    if (this.beepsCreated.length === 0) this.edited = false;
  }

  // * Save edited things
  onSave() {
    if (this.saving || (!this.edited && !this.clippingOutputSelected)) return;
    this.saving = true;

    this.openTemplateDialog(this.dialogLoaderRef);

    setTimeout(() => {
      this.templateDialog?.close();
      this.alertHelpers
        .showAlert({
          title: `<div class="text-primary-ash text-normal">${
            this.clippingActivated ? 'Clipping' : 'Beeping'
          } request has been sent!</div>`,
          icon: 'success'
        })
        .then(() => {
          const savedVideo = this.clippingActivated ? SessionData.clippingSavedVideo : SessionData.beepingSavedVideo;
          savedVideo['<CopyOf>k__BackingField'] = this.$currentSelection().file_type.includes('photo')
            ? this.detailsService.session_photo_object.photoId
            : this.detailsService.session_rec_object.videoId;

          this.detailsService.editedObject = savedVideo as any;

          if (this.clippingActivated) this.resetClipping();
          else this.resetBeeping();

          this.saving = false;
        });
    }, 1500);
  }

  saveClipping() {
    this.openTemplateDialog(this.dialogLoaderRef);
    const rec = this.detailsService.session_rec_object;

    const startTime = new Date(rec.startiTme);
    const endTime = new Date(rec.startiTme);

    const trimStartM = startTime.getMilliseconds() + parseInt(this.$editState().trimStart.toFixed(3).split('.')[1]);
    const trimEndM = startTime.getMilliseconds() + parseInt(this.$editState().trimEnd.toFixed(3).split('.')[1]);

    startTime.setSeconds(startTime.getSeconds() + this.$editState().trimStart);
    endTime.setSeconds(endTime.getSeconds() + this.$editState().trimEnd);
    startTime.setMilliseconds(trimStartM);
    endTime.setMilliseconds(trimEndM);

    const calculatedStartTrim = startTime
      .toLocaleString('sv', {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
        fractionalSecondDigits: 3
      })
      .replace(',', '.');
    const calculatedEndTrim = endTime
      .toLocaleString('sv', {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
        fractionalSecondDigits: 3
      })
      .replace(',', '.');

    this.clippingApiService
      .apiClippingCreateRecordingClipped(
        rec.videoId,
        calculatedStartTrim,
        calculatedEndTrim,
        this.outputType as 'A' | 'V' | 'AV'
      )
      .subscribe({
        next: (response: ClippingModel) => {
          this.templateDialog?.close();
          if (response.status) {
            this.alertHelpers
              .showAlert({
                title: '<div class="text-primary-ash text-normal"> Clipping request has been sent!</div>',
                icon: 'success'
              })
              .then(() => {
                this.detailsService.editedObject = response.SavedVideo;
                this.resetClipping();
                this.saving = false;
              });
          } else {
            this.alertHelpers
              .showAlert({
                title:
                  '<div class="text-primary-ash text-normal">Error while sending clipping request. Please try again later!</div>',
                icon: 'error'
              })
              .then(() => {
                this.saving = false;
              });
          }
        },
        error: (error: Error) => {
          this.alertHelpers.showAlert({
            title:
              '<div class="text-primary-ash text-normal">Error while sending clipping request. Please try again later!</div>',
            icon: 'error'
          });
          console.log('Clipping API error', error);
          this.saving = false;
        }
      });
  }

  saveBeeping() {
    this.openTemplateDialog(this.dialogLoaderRef);
    const rec = this.detailsService.session_rec_object;

    const startTime = new Date(rec.startiTme)
      .toLocaleString('sv', {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
        fractionalSecondDigits: 3
      })
      .replace(',', '.');
    const endTime = new Date(rec.startiTme)
      .toLocaleString('sv', {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
        fractionalSecondDigits: 3
      })
      .replace(',', '.');

    const beepingRequestParams: BeepingRequestModel = {
      RecID: rec.videoId,
      startDateTime: startTime,
      endDateTime: endTime,
      BeepingIntervals: this.beepTimings
    };

    this.beepingApiService.apiBeepingCreateRecordingBeeping(beepingRequestParams).subscribe({
      next: (response: BeepingResponseModel) => {
        this.templateDialog?.close();
        if (response.status) {
          this.alertHelpers
            .showAlert({
              title: '<div class="text-primary-ash text-normal"> Beeping request has been sent!</div>',
              icon: 'success'
            })
            .then(() => {
              this.detailsService.editedObject = response.SavedVideo;
              this.resetBeeping();
              this.saving = false;
            });
        } else {
          this.alertHelpers
            .showAlert({
              title:
                '<div class="text-primary-ash text-normal">Error while sending beeping request. Please try again later!</div>',
              icon: 'error'
            })
            .then(() => {
              this.saving = false;
            });
        }
      },
      error: (error: Error) => {
        this.alertHelpers.showAlert({
          title:
            '<div class="text-primary-ash text-normal">Error while sending beeping request. Please try again later!</div>',
          icon: 'error'
        });
        console.log('Beeping API error', error);
        this.saving = false;
      }
    });
  }

  // * Redact process
  checkAnyEditBeforeRedact() {
    if (this.cant_redact) return;

    if (this.edited || this.clippingOutputSelected) {
      this.dialogRef
        .open(ConfirmationModalComponent, {
          data: {
            title: 'You have unsaved changes in this file. Are you sure to discard these changes?'
          }
        })
        .afterClosed()
        .subscribe((result) => {
          if (result) {
            this.resetBeeping();
            this.resetClipping();
            this.activateRedact();
          }
        });
    } else {
      this.activateRedact();
    }
  }

  activateRedact() {
    const recordings = this.detailsService.session_object._recordings;

    // * Checking if the _recording already contains a processing redact file.
    const already_processing = recordings.filter(
      (rec) =>
        rec['<RedactedStatus>k__BackingField'] !== -1 &&
        rec['<RedactedStatus>k__BackingField'] !== 11 &&
        rec['<RedactedStatus>k__BackingField'] !== 13
    );

    // * if no file in process, then proceed for redact
    // * else show redact progress
    if (already_processing.length === 0) {
      const dialog = this.dialogRef.open(ConfirmationModalComponent, {
        data: {
          title: 'Are you sure you want to submit following file for redaction?',
          isRedact: true,
          filename: this.$currentSelection().file_id
        }
      });

      dialog.afterClosed().subscribe((result) => {
        if (result) {
          this.openTemplateDialog(this.dialogLoaderRef);

          setTimeout(() => {
            this.templateDialog?.close();
            this.alertHelpers
              .showAlert({
                title: '<div class="text-primary-ash text-normal">Redact request sent!</div>',
                icon: 'success'
              })
              .then(() => {
                const savedVideo = SessionData.redactSavedVideo;
                savedVideo['<CopyOf>k__BackingField'] = this.$currentSelection().file_type.includes('photo')
                  ? this.detailsService.session_photo_object.photoId
                  : this.detailsService.session_rec_object.videoId;

                this.detailsService.editedObject = savedVideo as any;
                this.redactActivated = !this.redactActivated;
              });
          }, 1000);
        }
      });
    } else {
      this.redactActivated = !this.redactActivated;
    }
  }

  reviewRedact() {
    if (this.redacted_status === 'detected') {
      this.openTemplateDialog(this.dialogLoaderRef);

      setTimeout(() => {
        window.open('', '_blank');
        this.templateDialog?.close();
      }, 1000);
    }
  }

  ngAfterViewChecked() {
    this.mediaState.updateMediaRefs({
      waveformCanvas: this.waveformCanvas,
      trackpadFramesContainerRef: this.trackpadFramesContainerRef
    });
  }

  ngOnInit() {
    this.editService.isAudioGenerated = false;

    setTimeout(async () => {
      if (!this.isAudioGenerated && this.$mediaRefs().mediaRef && this.$mediaRefs().waveformCanvas) {
        await this.editService.generateWaveform();
        this.editService.isAudioGenerated = true;
      }
    }, 1500);

    this.editService.editObservableCast.subscribe(async (command) => {
      if (command === 'on_file_select') {
        this.resetBeeping();
        this.resetClipping();
        this.edited = false;
        this.clippingOutputSelected = this.edited = false;
      } else if (command === 'loading') {
        this.isLoading = true;
      } else if (command === 'loaded') {
        this.isLoading = false;

        if (this.canShowEditingTools && !this.$errorState()._isFileError) {
          if (this.$mediaRefs().mediaRef && this.$mediaRefs().waveformCanvas && !this.isAudioGenerated)
            await this.editService.generateWaveform();
        }
      }
    });
  }
}
