import { Component, ElementRef, inject, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';

import { AuditLogApiService, COCLogApiService } from 'src/app/api';

import { SessionConstants } from 'src/app/constants/session-constants';
import { MediaPlayerComponent } from 'src/app/modules/shared/components/media-player/media-player.component';

import { DetailsService } from './details.service';
import { SessionService } from '../session.service';

import { MediaState } from 'src/app/modules/shared/state/media/media.service';
import { SessionData } from 'src/app/data/session.data';

@Component({
  selector: 'app-details',
  standalone: false,
  templateUrl: './details.component.html',
  styleUrl: './details.component.scss'
})
export class DetailsComponent {
  @ViewChild(MediaPlayerComponent) mediaPlayer!: MediaPlayerComponent;
  @ViewChild('metadataContainerRef') metadataContainerRef!: ElementRef;
  @ViewChild('tabsContainerRef') tabsContainerRef!: ElementRef;

  private readonly router = inject(Router);
  private readonly sessionService = inject(SessionService);
  private readonly detailsService = inject(DetailsService);
  private readonly mediaState = inject(MediaState);

  private readonly auditLogAPIService = inject(AuditLogApiService);
  private readonly cocLogAPIService = inject(COCLogApiService);

  // * Details service signals
  $currentSelection = this.detailsService.currentSelection;
  $cocLogState = this.detailsService.cocLogState;
  $errorState = this.detailsService.errorState;

  // * Media state signals
  $fileSource = this.mediaState.file_source;

  private readonly destroy$: Subject<void> = new Subject<void>();
  readonly fileLocations = SessionConstants.fileLocations;

  resizeObserver: ResizeObserver | null = null;
  isLoading = false;

  imageError: boolean;

  constructor() {
    this.imageError = false;

    // * Redirect to search page if currentState is not retained in details service(page refresh scenario)
    // * Handled in details.guard.ts

    const rec = this.detailsService.session_rec_object;
    const photo = this.detailsService.session_photo_object;
    const file_type = this.detailsService.currentSelection().file_type;

    // * Set file visibility condition
    this.detailsService.updateErrorState({
      _canShowFile: file_type.includes('photo')
        ? photo.fileLocation === this.fileLocations.dock || photo.fileLocation === this.fileLocations.central
        : rec.fileLocation === this.fileLocations.dock || rec.fileLocation === this.fileLocations.central
    });

    // * Set file source
    // this.mediaState.updateFileSource({
    //   videoSource: rec?.['<ResourceURL>k__BackingField'] ?? '',
    //   videoThumbnail: '',
    //   audioSource: rec?.['<ResourceURL>k__BackingField'] ?? '',
    //   imageSource: photo?.['<ResourceURL>k__BackingField'] ?? ''
    // });
  }

  get isEditTabAvailable() {
    const can_edit = !this.$currentSelection().file_type.includes('photo');
    return can_edit;
  }

  isLinkActive(url: any) {
    const baseUrl = this.router.url.replace('/AngularSession.aspx', '');
    return baseUrl === url;
  }

  onImageLoad(load: Event) {
    if (load) this.imageError = false;
  }

  onImageError(error: ErrorEvent) {
    if (error) this.imageError = true;
  }

  loadEvent(state: boolean) {
    this.isLoading = state;
  }

  logsEvent(selectingFile: boolean) {
    this.imageError = false;
    this.fetchLogs();

    // * Delay necessary for the media player component to render in the UI
    // * Else will produce an error when selecting a file
    setTimeout(() => {
      if (selectingFile) this.mediaPlayer.onSelectFile();
    });
  }

  fetchLogs() {
    this.detailsService.updateCOCLogState({
      _auditLogs: SessionData.auditLogs,
      _cocLogs: SessionData.cocLogs,
      _isLogsLoaded: true
    });

    // const file = this.$currentSelection().file_type.includes('photo')
    //   ? this.detailsService.session_photo_object
    //   : this.detailsService.session_rec_object;

    // * Triggering two API calls and updating logs at the same time
    // forkJoin({
    //   auditLogs: this.auditLogAPIService.apiAuditLogGetAuditLogByContextId(file.contextId),
    //   cocLogs: this.cocLogAPIService.apiCOCLogGetCOCLogByUniqueFileName(file.uniqueFileName)
    // })
    //   .pipe(takeUntil(this.destroy$))
    //   .subscribe({
    //     next: (response) => {
    //       this.detailsService.updateCOCLogState({
    //         _auditLogs: response.auditLogs,
    //         _cocLogs: response.cocLogs,
    //         _isLogsLoaded: true
    //       });
    //     },
    //     error: (err) => {
    //       console.error('Logs API error: ', err);
    //       this.detailsService.updateCOCLogState({
    //         _isLogsLoaded: true
    //       });
    //     }
    //   });
  }

  ngAfterViewInit() {
    setTimeout(() => {
      // window.scrollTo(1900, 500);
    }, 700);

    // * To observe the height changes for dynamically updating session list component
    this.resizeObserver = new ResizeObserver((entries) => {
      for (let entry of entries) {
        if (entry.target.classList.contains('tabs-container'))
          this.detailsService.updateComponentHeights({ tabComponent: entry.contentRect.height });
        else if (entry.target.classList.contains('metadata-container'))
          this.detailsService.updateComponentHeights({ metadataComponent: entry.contentRect.height });
      }
    });

    // * Observe both metadata & tabs component
    this.resizeObserver.observe(this.metadataContainerRef.nativeElement);
    this.resizeObserver.observe(this.tabsContainerRef.nativeElement);
  }

  ngOnInit() {
    this.fetchLogs();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();

    if (this.resizeObserver) this.resizeObserver.disconnect();
  }
}
