  get fileLocationsInSession() {
    const session = this.detailsService.session_object;
    let locationData: LocationData = {
      'Central server': 0,
      'Dock server': 0,
      Camera: 0
    };
    let highestFileLocation = '';

    // * Map through the recordings/photos to get all the file locations
    if (this.$currentSelection().file_type.includes('photo')) {
      session._photos.map((file) => {
        if (file.fileLocation === 0) locationData.Camera++;
        else if (file.fileLocation === 1) locationData['Dock server']++;
        else if (file.fileLocation === 2) locationData['Central server']++;
      });
    } else {
      session._recordings.map((file) => {
        if (file.fileLocation === 0) locationData.Camera++;
        else if (file.fileLocation === 1) locationData['Dock server']++;
        else if (file.fileLocation === 2) locationData['Central server']++;
      });
    }

    const max = Math.max(...Object.values(locationData));
    const key = Object.keys(locationData).find((key) => locationData[key as keyof LocationData]);
    highestFileLocation = `${key} (${max} ${max > 1 ? 'files' : 'file'})`;

    return { locationData, highestFileLocation };
  }

