  get isFormModified(): boolean {
    const $isModified = computed(() => {
      const initial = this.$initialData();
      const current = this.$formData();

      return JSON.stringify(initial) !== JSON.stringify(current);
    });

    return $isModified();
  }

  get charLeft(): number {
    return 300 - this.$formData().description.length;
  }

  onMetadataToggle() {
    this.metadataExpanded = !this.metadataExpanded;
  }

  onInputChange(value: string) {
    this.$formData.update((prev) => {
      return { ...prev, category: value };
    });
  }

  testOnSave() {}

  async onSave() {
    const response = await fetch('https://api.ipify.org');
    const ip = await response.text();

    this.taggedSessionApiService
      .apiTaggedSessionCheckIfSessionTagged(this.detailsService.session_object._vod_id)
      .subscribe({
        next: (IsTagged: boolean) => {
          const body: TagSessionRequest = {
            IsPriorityUpload: false,
            DefaultCategoryID: 0, // TODO: check
            CategoryID: this.categoryIdList[this.categoryList.indexOf(this.$formData().category)],
            categoryActionType: 'UploadToCentralServer', // TODO: check
            IsTagged,
            vodID: this.detailsService.session_object._vod_id,
            sessionID: this.detailsService.session_object._session_id,
            CaseNumber: this.$formData().case_number,
            Classified: this.$formData().restricted_access,
            Notes: '', // TODO: check
            desc: this.$formData().description,
            IPAddress: ip,
            name: this.$formData().name,
            MetaData: '' // TODO: check
          };

          this.taggedSessionApiService.apiTaggedSessionSaveTaggedSession(body).subscribe({
            next: (response) => {
              console.log(response);
              this.$initialData.set(this.$formData());
            },
            error: (error: Error) => {
              console.log('Error in tag a session API: ', error);
            }
          });
        },
        error: (error) => {
          console.log('Error in check tagged session API: ', error);
        }
      });
  }

